* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Rubik', sans-serif;
}

:root {
  --font-family: 'Rubik', sans-serif;
  --font-heading-family: Rubik, sans-serif;

  --color-131921: #131921;
  --color-3b4149: #3b4149;
  --color-febd69: #febd69;
  --color-232f3e: #232f3e;
  --color-footer: #232f3e;
  --color-orange: #bf4800;
  --color-f5f5f7: #f5f5f7;
  --color-ededed: #ededed;
  --color-777777: #777777;
  --color-bf4800: #bf4800;
  --color-1c1c1b: #1c1c1b;
}

.section-heading {
  font-size: 26px;
  line-height: 32px;
  font-weight: 500;
  margin-bottom: 30px;
  font-family: var(--font-family);
}

.card-wrapper {
  background-color: white;
  box-shadow: 0 0 10px #0000001a;
  padding: 15px;
}

a {
  text-decoration: none;
  display: inline-block;
}

.form-control {
  box-shadow: none;
  border-color: inherit;
}

.gap-5 {
  gap: 5px;
}

.gap-10 {
  gap: 10px;
}

.gap-15 {
  gap: 15px;
}

.gap-30 {
  gap: 30px;
}

.button {
  background-color: var(--color-232f3e);
  color: white;
  font-size: 14px;
  padding: 10px 30px;
  border-radius: 25px;
}

.button:hover {
  background-color: var(--color-febd69);
  color: #131921;
}

.buy {
  max-width: 130px;
}

.error {
  color: red;
}

/* ******************************* */
/* Header */
/* ******************************* */

.header-top-strip {
  background-color: var(--color-131921);
  border-bottom: 1px solid var(--color-3b4149);
}

.header-upper {
  background-color: var(--color-131921);
}

.header-upper .input-group-text {
  background-color: var(--color-febd69);
}

.input-group-text {
  background-color: var(--color-febd69);
}

.header-bottom {
  background-color: var(--color-232f3e);
}

.header-bottom .menu-links a,
.header-bottom .dropdown button {
  color: white;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  letter-spacing: 0.3;
  text-transform: uppercase;
}

.header-bottom .dropdown button:focus {
  box-shadow: none;
}

.header-bottom .dropdown-menu {
  background-color: var(--color-131921);
  width: 100%;
  transform: translate3d(0px, 57px, 0px) !important;
}
.header-bottom .dropdown-item {
  padding: 10px;
  margin-bottom: 3px;
  border-bottom: 1px solid var(--color-3b4149);
}

.header-bottom .dropdown-item:hover {
  background-color: transparent;
  color: var(--color-febd69) !important;
}

.header-upper-links {
  height: 35px;
  height: 35px;
}

/* ******************************* */
/* Footer */
/* ******************************* */
footer {
  background-color: var(--color-footer);
}

footer:not(:first-child) {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

footer .input-group-text {
  background-color: var(--color-232f3e);
  color: white;
  padding: 8px 17px;
  font-weight: 400;
}

/* ******************************* */
/* Home */
/* ******************************* */

.main-banner-content {
  top: 15%;
  left: 5%;
}

.main-banner-content h4 {
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color: var(--color-orange);
  margin: 0 0 12px;
  letter-spacing: 0.3px;
  text-transform: uppercase;
}

.main-banner-content h5 {
  font-size: 42px;
  line-height: 54px;
  letter-spacing: -2px;
  font-weight: 500;
  text-transform: none;
  font-family: var(--font-family);
  letter-spacing: 0.3px;
}

.main-banner-content p {
  font-size: 18px;
  line-height: 28px;
  letter-spacing: 0.4;
  color: var(--color-131921);
}

.small-banner {
  position: relative;
  width: 46%;
  overflow: hidden;
  transition: transform 0.3s;
}

.small-banner:hover {
  transform: scale(1.05);
}

.banner-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 0.3s;
}

.small-banner:hover .banner-overlay {
  opacity: 1;
}

.small-banner-content {
  top: 25%;
  left: 10%;
}

.small-banner-content h4 {
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  color: var(--color-orange);
  margin: 0 0 12px;
  letter-spacing: 0.3px;
  text-transform: uppercase;
}

.small-banner-content h5 {
  font-size: 22px;
  line-height: 34px;
  letter-spacing: -2px;
  font-weight: 500;
  text-transform: none;
  font-family: var(--font-family);
  letter-spacing: 0.3px;
}

.small-banner-content p {
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.4;
  color: var(--color-131921);
}

@media (max-width: 480px) {
  .small-banner-content h4 {
    font-size: 10px;
    margin-bottom: 0;
  }
  .small-banner-content h5 {
    font-size: 12px;
    margin-bottom: 0;
    line-height: 20px;
  }
  .small-banner-content p {
    font-size: 8px;
    line-height: 16px;
    letter-spacing: 0.2;
  }

  .main-banner-content h4 {
    font-size: 10px;
    font-weight: 400;
    line-height: 20px;
  }

  .main-banner-content h5 {
    font-size: 20px;
    line-height: 14px;
    letter-spacing: -2px;
    letter-spacing: 0.2px;
  }

  .main-banner-content p {
    font-size: 12px;
    line-height: 28px;
    letter-spacing: 0.4;
  }
}

/* ------------------------------ */
/* Home wrapper-2 */
/*-------------------------------- */

.home-wrapper-2 {
  background-color: var(--color-f5f5f7);
}

/* ******************************* */
/* Categories Section*/
/* ******************************* */

.categories {
  background-color: white;
  box-shadow: 0 0 10px #0000001a;
  padding: 15px;
}

.categories > div {
  width: 25%;
  padding: 10px 10px 10px 20px;
}

.categories > div > div {
  width: 60%;
}

.categories > div:nth-child(1),
.categories > div:nth-child(2),
.categories > div:nth-child(3),
.categories > div:nth-child(4) {
  border-bottom: 1px solid var(--color-ededed);
}

.categories > div:nth-child(1),
.categories > div:nth-child(2),
.categories > div:nth-child(3),
.categories > div:nth-child(5),
.categories > div:nth-child(6),
.categories > div:nth-child(7) {
  border-right: 1px solid var(--color-ededed);
}

/* ******************************* */
/* Blog */
/* ******************************* */

.blog-card {
  background-color: white;
  border-radius: 10px;
  height: 450px;
}

.blog-card img {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.blog-content {
  padding: 15px;
}

.blog-content p.date {
  font-size: 13px;
  line-height: 24px;
  text-transform: uppercase;
  letter-spacing: 0;
  font-weight: 400;
  color: var(--color-777777);
  padding: 0;
}

.blog-content h5 {
  font-size: 18px;
  color: var(--color-131921);
}
.blog-content p.desc {
  font-size: 13px;
  line-height: 22px;
  color: var(--color-777777);
}

.single-blog-card a {
  font-size: 18px;
  color: var(--color-777777);
  margin-bottom: 20px;
}
.single-blog-card h3 {
  font-size: 24px;
  font-weight: 600;
  color: var(--color-1c1c1b);
}
.single-blog-card p {
  color: var(--color-777777);
  font-size: 14px;
}

/* ******************************* */
/* Product Card */
/* ******************************* */

.product-card {
  padding: 15px;
  background-color: white;
  border-radius: 10px;
  overflow: hidden;
  width: 100%;
}

.product-card .product-image {
  height: 270px;
}

.product-card .product-image img {
  object-fit: contain;
}

.product-card .product-image > img:last-child {
  display: none;
}

.product-card:hover .product-image > img:nth-child(1) {
  display: none;
}

.product-card:hover .product-image > img:last-child {
  display: block;
}

.product-card:hover .action-bar {
  right: 15px;
}

.product-card .product-details h6 {
  color: var(--color-orange);
  font-size: 13px;
}

.product-card .product-details h5 {
  font-size: 15px;
  color: var(--color-1c1c1b);
}

.product-card .product-details p.price {
  font-size: 16px;
  color: var(--color-1c1c1b);
}

.product-card p.description {
  font-size: 13px;
  color: var(--color-777777);
  margin-right: 20px;
}

.action-bar {
  top: 10%;
  right: -23px;
  transition: 0.3s;
}

.wishlist-icon {
  top: 2%;
  right: 15px;
}

@media (max-width: 576px) {
  .product-card {
    width: 85%;
    /* width: calc(50% - 10px); */
    margin-right: 5%;
  }

  .product-image img {
    max-width: 100%;
    height: auto;
  }

  .brand {
    font-size: 14px;
  }

  .product-title {
    font-size: 16px;
  }

  .price {
    font-size: 14px;
  }

  .product-details {
    padding: 0;
  }
}

/* ------------------------------ */
/* Special Product */
/*-------------------------------- */

.special-product-card {
  padding: 20px 10px;
  border-radius: 10px;
  background-color: white;
}

.special-product-card .badge {
  background-color: #ef3939 !important;
  color: white;
}

/* ------------------------------ */
/* Famous Card */
/*-------------------------------- */

.famous-card {
  position: relative;
  overflow: hidden;
}

.famous-card .famous-content {
  top: 10%;
  left: 10%;
}

.famous-card img {
  border-radius: 10px;
}

.famous-content h5 {
  font-size: 13px;
  line-height: 20px;
  font-weight: 400;
  color: white;
  margin-bottom: 7px;
  text-transform: uppercase;
}
.famous-content h6 {
  font-size: 24px;
  line-height: 38px;
  font-weight: 500;
  color: white;
}
.famous-content p {
  font-size: 16px;
  line-height: 24px;
  font-weight: 100;
  color: white;
}

/* ------------------------------ */
/* Our Store */
/*-------------------------------- */

.filter-card {
  background-color: white;
  border-radius: 10px;
  padding: 15px 15px 25px;
}

.filter-title {
  font-size: 16px;
  line-height: 20px;
  font-weight: 600;
  color: var(--color-1c1c1b);
  margin-bottom: 20px;
}

.filter-card ul,
.filter-card .form-check-label {
  list-style: none;
  font-size: 13px;
  line-height: 30px;
  color: var(--color-777777);
  cursor: pointer;
}

.filter-card .form-check-input:checked {
  background-color: var(--color-febd69);
  border-color: var(--color-febd69);
}

.filter-card .form-check-input:focus {
  box-shadow: none;
  border-color: rgba(0, 0, 0, 0.25);
}

.sub-title {
  font-size: 14px;
  font-weight: 600;
  color: var(--color-1c1c1b);
  margin-bottom: 15px;
  margin-top: 20px;
  font-family: var(--font-family);
}

.colors {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.colors li {
  width: 20px;
  height: 20px;
  background-color: grey;
  border-radius: 50%;
}

.random-products h5 {
  font-size: 14px;
  margin-bottom: 8px;
}

.random-products:first-child {
  border-bottom: 1px solid var(--color-ededed);
}

.random-products {
  display: flex;
  flex-direction: column;
}

@media (min-width: 576px) {
  .random-products {
    flex-direction: row;
  }
}

@media (min-width: 768px) {
  .random-products .w-50 {
    width: 50%;
  }

  .random-products .w-25 {
    width: 20%;
  }
}

.filter-sort-grid {
  padding: 10px;
  border-radius: 10px;
  background-color: white;
}
.grid img {
  width: 35px;
  height: 35px;
  background-color: #f2f2f2;
  padding: 8px;
  border-radius: 3px;
  cursor: pointer;
}

.gr-12 {
  width: 100%;
}

.gr-6 {
  width: 49%;
}

.gr-4 {
  width: 32%;
}

.gr-3 {
  width: 24%;
}

.gr-6 .product-image {
  width: 100%;
}

.gr-6 .product-image img {
  margin: auto;
  display: block;
}

.gr-12 .product-card {
  display: flex;
  gap: 20px;
}

.gr-12 .product-card .product-image {
  width: 270px;
  height: auto;
}

.gr-12 .action-bar {
  top: 17%;
}

@media (max-width: 1199.98px) {
  .gr-6 {
    width: 49%;
  }
}

@media (max-width: 991.98px) {
  .gr-4 {
    width: 49%;
  }
}

@media (max-width: 767.98px) {
  .gr-6 {
    width: 100%;
  }

  .gr-4 {
    width: 100%;
  }

  .gr-3 {
    width: 38%;
  }

  .gr-12 .product-card {
    flex-direction: column;
  }

  .gr-12 .product-card .product-image {
    width: 100%;
  }

  .gr-12 .action-bar {
    top: auto;
    bottom: 10px;
    right: 10px;
  }
}

@media (max-width: 575.98px) {
  .gr-4 {
    width: 48%;
  }
  .product-card {
    width: auto;
  }

  .product-card .product-image {
    width: 60%;
    height: 120px;
  }

  .gr-3 {
    width: 100%;
  }

  .gr-12 .product-card {
    flex-direction: column;
  }

  .gr-12 .product-card .product-image {
    width: 100%;
  }

  .gr-12 .action-bar {
    top: auto;
    bottom: 10px;
    right: 10px;
  }
}

/* ------------------------------ */
/* Contact Page */
/*-------------------------------- */

.contact-inner-wrapper {
  padding: 30px 20px;
  border-radius: 15px;
  background-color: white;
  gap: 15px;
}

.contact-inner-wrapper > div {
  width: 48%;
}

.contact-title {
  font-size: 26px;
  font-weight: 500;
  line-height: 35px;
  text-align: left;
  font-family: var(--font-family);
}

.contact-inner-wrapper form .form-control {
  background-color: var(--color-f5f5f7);
  border-radius: 10px;
  border-color: transparent;
  padding: 10px;
}

.contact-inner-wrapper ul {
  list-style: none;
}

.contact-inner-wrapper address,
.contact-inner-wrapper a,
.contact-inner-wrapper p {
  color: var(--color-777777);
}

/* ------------------------------ */
/* Compare product */
/*-------------------------------- */

.compare-product-card {
  padding: 20px 15px;
  background-color: white;
  border-radius: 10px;
}

.compare-product-card .cross,
.wishlist-card .cross {
  top: 3%;
  right: 8%;
  width: 25px;
  padding: 3px;
  cursor: pointer;
}
.compare-product-details h5.title,
.wishlist-card h5.title {
  font-size: 15px;
  line-height: 22px;
  font-weight: 500;
  color: var(--color-1c1c1b);
  font-family: var(--font-family);
}
.compare-product-details h6.price,
.wishlist-card h6.price {
  font-size: 15px;
  line-height: 22px;
  font-weight: 500;
  font-family: var(--font-family);
}
.compare-product-card .product-detail {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0px;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}

/* ------------------------------ */
/* Auth Card */
/*-------------------------------- */

.auth-card {
  padding: 20px;
  background-color: white;
  width: 500px;
  margin: 30px auto;
  border-radius: 10px;
}

.auth-card h3 {
  font-size: 19px;
  font-weight: 500;
  color: var(--color-777777);
}

.auth-card p {
  font-size: 13px;
  color: var(--color-777777);
}

.auth-card .form-control {
  height: 50px;
  background-color: var(--color-f5f5f7);
  color: var(--color-777777);
  font-size: 14px;
  border-radius: 10px;
  border-color: transparent;
}

.auth-card a {
  font-size: 14px;
  font-weight: 400;
  color: var(--color-1c1c1b);
}

@media (max-width: 768px) {
  .auth-card {
    width: 80%;
  }
}

@media (max-width: 576px) {
  .auth-card {
    width: 90%;
    padding: 15px;
  }
}

.signup {
  background-color: var(--color-febd69);
  color: var(--color-131921);
  border: none;
}
.signup:hover {
  background-color: var(--color-131921);
  color: white;
}

/* ------------------------------ */
/* Policy pages */
/*-------------------------------- */

.policy {
  background-color: white;
  padding: 30px;
}

/* ------------------------------ */
/* Product detail page */
/*-------------------------------- */
/* Common styles */
.description-wrapper h4,
.reviews-wrapper h4 {
  font-size: 26px;
  color: #1c1c1b;
  font-family: var(--font-family);
  margin-bottom: 10px;
}

.description-wrapper p {
  font-size: 14px;
  color: var(--color-777777);
}

.review-inner-wrapper {
  background-color: white;
  padding: 30px;
}

.review-head {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  padding: 20px;
}

.review-head h4 {
  font-size: 18px;
  color: var(--color-1c1c1b);
}

.review-head p,
.review-head a {
  font-size: 14px;
  color: var(--color-777777);
}

.review-form {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.review-form h4 {
  font-size: 16px;
  color: var(--color-777777);
}

.review:not(:last-child) {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.review p {
  font-size: 14px;
  color: var(--color-777777);
}

/* Media queries for responsiveness */

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
  .description-wrapper h4,
  .reviews-wrapper h4 {
    font-size: 22px;
  }
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {
  .description-wrapper h4,
  .reviews-wrapper h4 {
    font-size: 24px;
  }
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) {
  .description-wrapper h4,
  .reviews-wrapper h4 {
    font-size: 28px;
  }
}

.main-product-image {
  padding: 20px;
  background-color: white;
  border-radius: 10px;
}

.main-image > div {
  border: 1px solid rgba(0, 0, 0, 0.25);
}

.main-product-image img {
  width: 100% !important;
  height: 500px !important;
  padding: 10px;
}

.other-product-images {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}

.other-product-images .other-image {
  width: calc(33.33% - 15px);
}

.other-product-images .other-image img {
  max-width: 100%;
  height: auto;
}

@media (min-width: 1100px) {
  .other-product-images .other-image {
    width: calc(25% - 15px); /* 4 columns in a row for larger screens */
  }

  .other-product-images .other-image img {
    width: 100%;
    max-height: 150px;
    object-fit: cover;
  }
}

@media (max-width: 1100px) {
  .other-product-images .other-image {
    width: calc(50% - 15px);
  }

  .other-product-images .other-image img {
    max-width: 100%;
    max-height: 220px;
  }
}

@media (max-width: 990px) {
  .other-product-images .other-image {
    width: calc(50% - 15px);
  }

  .other-product-images .other-image img {
    max-width: 80%;
    max-height: 200px;
  }
}

@media (max-width: 578px) {
  .other-product-images .other-image {
    width: calc(50% - 15px);
  }

  .other-product-images .other-image img {
    max-width: 60%;
    max-height: 150px;
  }
}

.main-product-image .js-image-zoom__zoomed-image {
  left: 55px !important;
  padding: 10px;
  background-color: white;
  border-radius: 10px;
  z-index: 1000;
}

.main-product-details {
  padding: 30px 20px;
  background-color: white;
  border-radius: 10px;
}

.border-bottom {
  border-bottom: 1px solid rgba(0, 0, 0, 0.25);
}

.main-product-details h3.title {
  font-size: 18px;
  font-weight: 600;
  color: var(--color-1c1c1b);
  margin-bottom: 10px;
}

.main-product-details p.price {
  font-size: 16px;
  line-height: 20px;
  font-weight: 600;
  margin-top: 10px;
}

.main-product-details a.review-btn,
.t-review {
  color: var(--color-777777);
  font-size: 13px;
}

.product-heading {
  font-size: 14px;
  color: var(--color-1c1c1b);
  margin-bottom: 0px;
}

.product-data {
  font-size: 13px;
  columns: var(--color-777777);
  margin-bottom: 0px;
}

.main-product-details a {
  font-size: 14px;
  color: var(--color-777777);
}

/* ------------------------------ */
/* Cart */
/*-------------------------------- */

.cart-header {
  display: none;
}

.cart-data {
  margin-bottom: 20px;
}

.cart-col-1 {
  width: 100%;
}

.cart-col-2,
.cart-col-3,
.cart-col-4 {
  display: none;
}

@media (min-width: 576px) {
  .cart-col-2,
  .cart-col-3,
  .cart-col-4 {
    display: block;
  }
}

@media (min-width: 768px) {
  .cart-header {
    display: flex;
  }

