/*  sort bar */

/* Media Queries for smaller screens */
@media (max-width: 767.98px) {
  .filter-sort-grid {
    flex-direction: column;
    gap: 1rem;
  }

  .filter-sort-grid .d-flex {
    flex-direction: column;
    gap: 1rem;
  }

  .filter-sort-grid .d-flex:last-child {
    margin-top: 1rem;
  }

  .filter-sort-grid .d-flex .form-control {
    width: auto;
  }
}

/* Media Queries for medium screens */
@media (min-width: 768px) and (max-width: 991.98px) {
  .filter-sort-grid .d-flex {
    flex-direction: column;
    gap: 1rem;
  }

  .filter-sort-grid .d-flex:last-child {
    margin-top: 1rem;
  }
}

/* Loader */
.loader {
  border: 4px solid var(--color-febd69);
  border-top: 4px solid white;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
